import React, { Component } from "react";
import { Form, Input, Select, Button, Radio, InputNumber } from "antd";
import { handleForm } from "@/redux/form.redux";
import { connect } from "react-redux";
import { restoreFormData } from "../../../utils/productUtil";
const { Option } = Select;

class AddStep extends Component {
  state = {
    confirmDirty: false,
    loading: false,
    levels: this.props.formData ? this.props.formData.memberLevel : 0,
    onSale: "yes",
    shipping: "auto",
    sendMail: "no",
    formData: null
  };
  UNSAFE_componentWillMount() {
    let url = document.location.toString();
    // console.log("heelo");
    let idArr = url.split("/");
    let id = idArr[idArr.length - 1];
    if (!isNaN(parseInt(id))) {
      // console.log(this.props.allProducts[id - 1], [id - 1]);
      //productUitl
      // if (typeof this.props.allProducts[id - 1].contact !== "string") {
      // console.log("hello");
      this.setState({
        formData: restoreFormData(this.props.allProducts[id - 1])
      });
      // }

      this.setState({ levels: this.props.allProducts[id - 1].memberLevel });
    }
    // console.log(id, "id");
  }
  handleProductInfo = productInfo => [];

  onLevelChange = value => {
    this.setState({ levels: parseInt(value) });
  };
  onDescChange = ({ target: { value } }) => {
    this.setState({ value });
  };
  onShippingChange = e => {
    // console.log("radio checked", e.target.value);
    this.setState({
      shipping: e.target.value
    });
  };
  onSaleChange = e => {
    // console.log("radio checked", e.target.value);
    this.setState({
      onSale: e.target.value
    });
  };
  onSendMail = e => {
    // console.log("radio checked", e.target.value);
    this.setState({
      sendMail: e.target.value
    });
  };
  onFinish = values => {
    // console.log("Success:", values);
    this.props.handleFormData(values);
    this.props.handleForm(values);
    this.props.handleNext();
    this.setState({ formData: values });
  };

  onFinishFailed = errorInfo => {
    console.log("Failed:", errorInfo);
  };
  render() {
    let formData;
    if (this.props.formData) {
      formData = this.props.formData;
    } else if (this.state.formData) {
      formData = this.state.formData;
    }
    console.log(formData);
    const renderLevelDesc = () => {
      let arr = [];
      for (let i = 1; i <= this.state.levels; i++) {
        arr.push(i);
      }
      // console.log(arr);
      return arr.map(item => {
        return (
          <div key={item}>
            <Form.Item
              name={`levelName${item}`}
              label={`${item}`}
              rules={[
                {
                  required: true,
                  message: `${item}`
                }
              ]}
            >
              <Input placeholder={""} />
            </Form.Item>
            <Form.Item label={`${item}`}>
              <Input.Group compact>
                <Form.Item name={[`levelPrice${item}`, `price${item}`]} noStyle>
                  <InputNumber
                    min={0}
                    step={1}
                    style={{
                      width: "calc(100% - 100px)"
                    }}
                    placeholder={`${item}`}
                  />
                </Form.Item>
                <Form.Item
                  name={[`levelPrice${item}`, `unit${item}`]}
                  noStyle
                  rules={[
                    { required: true, message: `${item}` }
                  ]}
                >
                  <Select
                    style={{
                      width: 100
                    }}
                    placeholder=""
                  >
                    <Option value=""></Option>
                    <Option value="3">3</Option>
                    <Option value="6">6</Option>
                    <Option value=""></Option>
                    <Option value=""></Option>
                  </Select>
                </Form.Item>
              </Input.Group>
            </Form.Item>
            <Form.Item
              name={`levelDesc${item}`}
              label={`${item}`}
              rules={[
                {
                  required: true,
                  message: ""
                }
              ]}
            >
              <Input.TextArea
                id="desc"
                onChange={this.onDescChange}
                placeholder=""
                autoSize={{ minRows: 3, maxRows: 5 }}
              />
            </Form.Item>
            <Form.Item label={`${item}`} name={`levelLimit${item}`}>
              <InputNumber
                style={{ width: "100%" }}
                min={0}
                step={1}
                placeholder=""
              />
            </Form.Item>
            <Form.Item name={`levelNote${item}`} label={`${item}`}>
              <Input.TextArea
                id="note"
                onChange={this.onDescChange}
                placeholder=""
                autoSize={{ minRows: 3, maxRows: 5 }}
              />
            </Form.Item>
          </div>
        );
      });
    };

    const formItemLayoutWithOutLabel = {
      wrapperCol: {
        xs: { span: 24, offset: 12 },
        sm: { span: 20, offset: 10 }
      }
    };
    const formItemLayout = {
      labelCol: {
        sm: { span: 8 }
      },
      wrapperCol: {
        sm: { span: 8 }
      }
    };
    return (
      <div className="shadow-radius" style={{ marginTop: "50px" }}>
        <Form
          {...formItemLayout}
          onFinish={this.onFinish}
          onFinishFailed={this.onFinishFailed}
          initialValues={formData ? formData : null}
        >
          <Form.Item
            label=""
            name="productName"
            rules={[
              {
                required: true,
                message: ""
              }
            ]}
          >
            <Input placeholder="" />
          </Form.Item>
          <Form.Item
            name="productInfo"
            label=""
            rules={[
              {
                required: true,
                message: ""
              }
            ]}
          >
            <Input.TextArea
              onChange={this.onDescChange}
              placeholder="20"
              autoSize={{ minRows: 3, maxRows: 5 }}
            />
          </Form.Item>

          <Form.Item
            label=""
            name="memberLevel"
            rules={[
              {
                required: true,
                message: ""
              }
            ]}
          >
            <Select
              placeholder=""
              onChange={this.onLevelChange}
            >
              <Option value="1">1</Option>
              <Option value="2">2</Option>
              <Option value="3">3</Option>
              <Option value="4">4</Option>
            </Select>
          </Form.Item>
          {renderLevelDesc()}
          <Form.Item
            label=""
            name="sendMail"
            rules={[
              {
                required: true,
                message: ""
              }
            ]}
          >
            <Radio.Group onChange={this.onSendMail} value={this.state.sendMail}>
              <Radio value="yes"></Radio>
              <Radio value="no"></Radio>
            </Radio.Group>
          </Form.Item>
          <Form.Item
            label=""
            name="shippingMethod"
            rules={[
              {
                required: true,
                message: ""
              }
            ]}
          >
            <Radio.Group
              onChange={this.onShippingChange}
              value={this.state.shipping}
            >
              <Radio value="auto" defaultChecked={true}>
                
              </Radio>
              <Radio value="manual"></Radio>
            </Radio.Group>
          </Form.Item>
          <Form.Item
            label=""
            name="onSale"
            rules={[
              {
                required: true,
                message: ""
              }
            ]}
          >
            <Radio.Group onChange={this.onSaleChange} value={this.state.onSale}>
              <Radio value="yes"></Radio>
              <Radio value="no"></Radio>
            </Radio.Group>
          </Form.Item>
          <Form.Item
            name={`contact`}
            label={``}
            rules={[
              {
                required: true,
                message: ""
              }
            ]}
          >
            <Input.TextArea
              id="contact"
              onChange={this.onDescChange}
              placeholder=""
              autoSize={{ minRows: 3, maxRows: 5 }}
            />
          </Form.Item>
          <Form.Item {...formItemLayoutWithOutLabel}>
            <Button type="primary" htmlType="submit">
              
            </Button>
          </Form.Item>
        </Form>
      </div>
    );
  }
}
const mapStateToProps = state => {
  return {
    formData: state.form.formData,
    allProducts: state.product.allProducts
  };
};
const actionCreator = {
  handleForm
};
export default connect(mapStateToProps, actionCreator)(AddStep);
